<template>
  <header>
    <nav>
      <h1><router-link to="/"> TRNN! </router-link></h1>
      <ul>
        <li><router-link to="/exchange">Exchanges </router-link></li>
        <li><router-link to="/register">Register Exchange </router-link></li>
        <li v-if="!logged_in"><router-link to="/login">Login </router-link></li>
        <li v-if="logged_in"><router-link to="/" @click="logout">LogOut </router-link></li>
      </ul>
    </nav>
  </header>
</template>
  
<script>
export default {
  data() {
    return {
      logged_in: false,
    }
  },
  created() {
    this.logged_in = localStorage.getItem('Authorization');
  },
  methods: {
    logout() {
      localStorage.removeItem('Authorization');
      this.logged_in = false;
      this.$router.push('/');
    }
  }
}
</script>
  
